//    & CSS
import { useEffect, useState } from "react";

// style & img
import styled, { css } from "styled-components";
import infoIcon from "../../../assets/img/icon/search-s.png"

/**
 * @param {string} text   
 * @param {string} type   (button, submit)
 * @param {string} errorMessage   
 * @param {() => void} handleClick    , Route   history.push   .
 * @param {string} buttonColor   
 * @param {string} hoverColor    
 * @param {object} props   
 */

/*
    props   
    - text, type, disabled, handleClick, theme, size, width, height, bordercolor, name
*/

const Button = ({ text, type = "button", disabled, handleClick, theme, size, width, height, bordercolor, name }) => {
    return (
        <>
            <Wrapper width={width}>
                <ButtonStyle
                    onSubmit={e => e.preventDefault()}
                    type={type}
                    disabled={disabled}
                    handleClick={handleClick}
                    theme={theme}
                    size={size}
                    bordercolor={bordercolor}
                    width={width}
                    height={height}
                    name={name}>
                    {text}
                </ButtonStyle>
		</Wrapper>
        </>
    );
};

const Wrapper = styled.div`
	display: ${props => props.display || 'inline-block'};
	width: ${props => props.width};
	/* text-align: center; */
`;

const sizes = {
	large: {
		width: '100%',
		fontSize: '17px',
		padding: '16px 20px',
		fontWeight: '600',
		height: '50px',
	},
};

const sizeStyles = css`
	${({ size }) => css`
		width: ${sizes[size].width};
		font-size: ${sizes[size].fontSize};
		border-radius: ${sizes[size].borderRadius};
		padding: ${sizes[size].$padding};
		font-weight: ${sizes[size].fontWeight};
		height: ${sizes[size].height};
	`}
`;

const ButtonStyle = styled.button`
	border-radius: 5px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: ${props => props.width || '80px'};
	height: ${props => props.height || '30px'};
	min-width: fit-content;
	color: #2c2c2c;
	font-size: 14px;
	${props => props.size && sizeStyles}
    
    //  
	${props =>
		props.theme === 'line' &&
		css`
			border: 1px solid #2c2c2c;
			background: transparent;
		`}

    //  
    ${props =>
		props.theme === 'primary' &&
		css`
			background: #2c2c2c;
			color: #fff;
		`}
		${props =>
		props.theme === 'disable' &&
		css`
			background: #d9d9d9;
			color: #fff;
		`}

    //  
    ${props =>
		props.theme === 'moreInfo' &&
		css`
			border: 1px solid ${props.bordercolor};
			background: url(${infoIcon}) 50% 50% no-repeat;
			border: 1px solid #d9d9d9;
			width: 35px;
			height: 35px;
		`}

    //  
    ${props =>
		props.theme === 'login' &&
		css`
            background-image: linear-gradient(90deg, #58D6F0, rgba(34, 207, 186, 0.3));
            background-size: cover;
			color: #fff;
			width: 263px;
			height: 58px;
            border-radius: 50px;
		`}
`;

export default Button;